import React from 'react';
import Sidebar from './Sidebar';
import WaitingRoom from './WaitingRoom'
import './App.css';
import { createBrowserHistory } from "history";
import { Router, Route } from "react-router";
import SelectorRoom from './SelectorRoom';
import Agree from './Agree';
import Story from './Story';

const browserHistory = createBrowserHistory();

class App extends React.Component {
  constructor(props) {
    super(props);
  }
  
  render() { 
    return (
      <div className="App" id="outer-container">
      <Router history={browserHistory}>
        <Sidebar pageWrapId={'page-wrap'} outerContainerId={'outer-container'} />
        <Route path="/" component={() => <Agree history = {browserHistory}/>} />
        <Route path="/story" component={() => <Story history = {browserHistory}/>} />
        <Route path="/wait_room" component={() => <WaitingRoom name={'незнакомец'} history = {browserHistory}/>} />
        <Route path="/selector" component={SelectorRoom} />
      </Router>
      </div>
    );
  }
}

export default App;